.arraw-icon{
    margin: 10px;
    cursor: pointer;
}

.arraw-icon:hover{
    color: rgb(37, 138, 200);
}

.coverImage{
    height: 200px;
    width: 600px;
}

.coverImageContainer{
    position: relative;
}

.hoverCoverImage{
    position: absolute;
    top: 0%;
    background-color: rgba(0,0,0,0.6);
    width: 100%;
    height: 98%;
    opacity: 0%;
    display: flex;
    justify-content: center;
    align-items: center;
}


.hoverCoverImage:hover{
    opacity: 100%;
}


.photoIcon{
    color: white;
    font-size: 35px !important;
    opacity: 40%;
    margin-right: 20px;
    cursor: pointer;
}


.photoIcon:hover{
    opacity: 100% ;
}

.photoIconDisabled{
    cursor: not-allowed;
}

.userInfo{
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.heading-4{
    margin-top: -40px;
    font-size: 19px;
    margin-left: 50px;
    margin-bottom: 10px;
    justify-content: space-between;
}


.heading-3{
    margin-left: 20px;
    font-size: 24px;
}

.usernameSection{
    margin-left: 20px;
    color: darkgray;
}

.mainProfile{
    background-color: rgb(207,206,206);
    margin-top: 0;
    border: 1px solid white;
}

/* .profile-bio{
    height: 100%;
} */


.avatarContainer{
    margin-top: -80px;
    margin-left: 20px;
    border-radius: 100%;
    position: relative;
    height: 150px;
    width: 150px;
    border: 4px solid white;
}


.avatar{
    position: relative;
    width: 100%;
    height: 98%;
    border-radius: 100%;
}


.hoverAvatarImage{
    position: absolute;
    top: 0%;
    background-color: rgba(0,0,0,0.6);
    opacity: 0%;
    display: flex;
    justify-content: center;
    align-items: center;
}


.hoverAvatarImage:hover{
    opacity: 100%;
}

.tweet{
    background-color: white;
    width: 20%;
    height: 20%;
    padding: 2rem 2rem;
    color: skyblue;
}

.tweetsText{
    margin-top: 20px;
    margin-left: 20px;
    margin-bottom: 5px;
}

.infoContainer{
    margin-left: 20px;
    margin-top: 10px;
}

.LocationAndLink{
    display: flex;
    align-items: center;
    gap: 20px;
    margin-top: 10px;
}

.subInfo{
    display: flex;
    align-items: center;
    gap: 5px;
}

.center{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}


.sidebar-content-wrapper {
    overflow: scroll;
    height: 100%;
}

.sidebar-content-wrapper::-webkit-scrollbar {
    display: none;
}